import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//

import { ComponentViewComponent } from './component-view/component-view.component';//
import { ComponentConfigComponent } from './component-config/component-config.component';//
import { ComponentModifyComponent } from './component-modify/component-modify.component';//-
const routes: Routes = [
    {
        path: 'component-view', component: ComponentViewComponent,
    },
    {
        path: 'component-config', component: ComponentConfigComponent,
    },
    {
        path: 'component-modify', component: ComponentModifyComponent,
    },
];

export const ModuleManageRouting: ModuleWithProviders = RouterModule.forChild(routes);
